import react from 'react';
import React, { useState } from 'react';
import FormData from './Form';
import {Link} from 'react-router-dom';


const Registration = () => {
    const [users, SetUsers] = useState([])

    const AddUsers =(name, surname, location, age)=>{
        SetUsers([...users, {
            uid: users.length,
             _name: name,
            _surname: surname,
            _location: location,
            _age: age

        }])
    }

   return(
    <div className="Forms">
        <FormData AddUsers = {AddUsers}/>
        <Link to ='./Users'>{users.map(action => <p> {action.uid + 1} Hi i am a Programmer{action._name} {action._surname} {action._location} {action._age}</p>)}  
       </Link>

       </div>
)
}
export default Registration;