import React, { Component } from 'react';

export default  class Users extends Component{
    state = {

        arrList : [
                {
                    name: 'Rams',
                    surname:'Loliwe',
                    location:'Seshego',
                    age:78,
                },
                {
                    name: 'Louisa',
                    surname:'Rabelani',
                    location:'Seshego',
                    age:30,
                },
                {
                    name: 'Maanda',
                    surname:'Nunga',
                    location:'Seshego',
                    age:27,
                }
            ]
       

    }

    

    render(){

        return (
            <div className = "Userlist">
                 <h1>Users</h1>
                 <h3>User list</h3>
                 <h5>{this.state.arrList.map(el=> <li>{el.name +"    :   "+ el.surname  + "    :   "+ el.age}</li>)} </h5>
            </div>
        )
    }
}






