import React from 'react';
import { useState } from 'react';
import './index.css';


const Profile = () => {
  const ChangeValue =()=>{
    setCount(10)
}
const [Count,setCount] = useState(7)


const [items,setItems] = useState([])
const setItemsArray = ()=>{
    setItems([...items,{
        uid :items.length,
        name: "item number :" +items.length
    }])
}
     return(
         <>
         <div className ="container">
           <p>Welcome to my profile</p>
           <ul>
               <li>Rangolo Mueni</li>
               <li>Rangalomueni@gmail.com</li>
               <li>0876563475</li>
               <li>Matangari tshikwarani</li>
               </ul>
               <p>i am default value {Count}</p> 
          <button onClick={ChangeValue} > Count value </button>

          <button onClick={setItemsArray}> Array items</button>
          
            {items.map( el=> <li key={el.uid}>{el.name}</li>)}
               </div>
         </>
     )

}

export default Profile;